@import url(https://fonts.googleapis.com/css?family=Bitter&display=swap);
body {
  margin: 0;
  font-family: 'Bitter', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  width: 100%;
}
.setting{
  width: 100%;
  height: 100px;
  background-color: black;
  display: grid;
  grid-auto-flow: column;
  justify-content: center;
  align-items: center;
}
.setting input{
  margin: 10px;
  outline: none;
  border-width: 0px;
  border-radius: 6px;
  font-size: 16px;
  padding: 4px;
}
.wrapSort,
.wrapYearsForm{
  display: grid;
  justify-items: center;
  margin-bottom: 13px;
}
.wrapSort,
.wrapYearsForm>label{
  color: silver;
  font-size: 12px;
}
.wrapYearsForm>input{
  width: 145px;
}
.OneOfMovie{
  width: 100%;
  height: 300px;
  display: grid;
  grid-template-columns: 1fr 2fr 2fr 2fr;
  background-color: silver;
  align-items: center;
  justify-content: center
}
.OneOfMovie::after{
  display: block;
  content: '';
  grid-column: 1/5;
  border-radius: 50%;
  width: 70%;
  height: 1px;
  background: -webkit-gradient(linear, left top, right top, from(silver), color-stop(rgba(0,0,0,0.5)), to(silver));
  background: -webkit-linear-gradient(left, silver, rgba(0,0,0,0.5), silver);
  background: linear-gradient(to right, silver, rgba(0,0,0,0.5), silver);
  justify-self: center;
}
.moviePoster{
  display: grid;
  align-items: center
}
.moviePoster>img{
  width: 200px;
  height: 295px;
}
.movieTitle{
  display: grid;
  justify-content: center;
  padding: 15px;
}
.movieYear{
  display: grid;
  justify-content: center;
  font-size: 18px;
}
.Similar>p{
  margin: 7px;
}

.pagination{
  width: 100%;
  height: 50px;
  background-color: black;
  display: grid;
  justify-content: center;
  align-items: center;
  grid-auto-flow: column;
}
.pagination :first-child{
  border-radius: 10px 0px 0px 0px;
}
.pagination :last-child{
  border-radius: 0px 0px 10px 0px;
}
.pageLink{
  color: white;
  text-decoration: none;
  padding: 5px 10px;
  border: 1px solid white;
}
.pageLink:hover{
  background-color: rgba(192,192,192, 0.45);
  -webkit-transition: 0.12s;
  transition: 0.12s;
}



